/**
 * Test for simple SessionProfiler without EventEmitter
 */

import { SessionProfiler } from '../../src/engine/SessionProfiler.simple';

describe('SessionProfiler - Simple2', () => {
  it('should be able to create an instance', () => {
    const profiler = new SessionProfiler();
    expect(profiler).toBeDefined();
    expect(profiler).toBeInstanceOf(SessionProfiler);
  });

  it('should have required methods', () => {
    const profiler = new SessionProfiler();
    expect(typeof profiler.getSessionState).toBe('function');
    expect(typeof profiler.isKillzone).toBe('function');
  });
});